import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import axios from '../../utils';

export const fetchUser = createAsyncThunk('/users/fetch', async () => {
  const token = localStorage.getItem('ACCESS_TOKEN');
  const userId = localStorage.getItem('USER');

  if (token && userId) {
    try {
      const results = await axios.get(`/users/${userId}`);
      return { id: userId, ...results };
    } catch (err) {
      console.log('fetchUser failed', err);
      return {};
    }
  } else {
    return {};
  }
});

export const userSlice = createSlice({
  name: 'user',
  initialState: {
    isLoggedIn: !!localStorage.getItem('USER'), // TODO: storage token 정보로 판단하게 변경
    isRequireAuth: false,
    user: {},
  },
  reducers: {
    testlogin: (state, action) => {
      const { payload } = action;
      state.isLoggedIn = payload;
    },
    login: (state, action) => {
      const { payload } = action;
      const { data } = payload;
      const { token, user } = data;
      const { nickname, phone, images, leisure, age, gender, car, region } =
        user;

      localStorage.setItem('ACCESS_TOKEN', token.access_token);
      localStorage.setItem('REFRESH_TOKEN', token.refresh_token);
      localStorage.setItem('USER', user.id);

      state.isRequireAuth =
        !nickname ||
        !phone ||
        images === null ||
        leisure === null ||
        images.length === 0 ||
        leisure.length === 0 ||
        !age ||
        gender === null ||
        car === null ||
        region === null;
      state.isLoggedIn = true;
      state.user = user;
    },
    logout: state => {
      localStorage.setItem('ACCESS_TOKEN', '');
      localStorage.setItem('REFRESH_TOKEN', '');
      localStorage.setItem('USER', '');

      state.isLoggedIn = false;
      state.isRequireAuth = false;
      state.user = {};
    },
  },
  extraReducers: builder => {
    builder
      .addCase(fetchUser.pending, state => {
        state.isLoading = true;
      })
      .addCase(fetchUser.fulfilled, (state, action) => {
        const user = action.payload.data;
        const { nickname, phone, images, leisure, age, gender, car, region } =
          user;
        if (user?.id) {
          state.isLoading = false;
          state.user = user;
          state.isLoggedIn = true;
          state.isRequireAuth =
            !nickname ||
            !phone ||
            images === null ||
            leisure === null ||
            images.length === 0 ||
            leisure.length === 0 ||
            !age ||
            gender === null ||
            car === null ||
            region === null;
        } else {
          localStorage.setItem('ACCESS_TOKEN', '');
          localStorage.setItem('REFRESH_TOKEN', '');
          localStorage.setItem('USER', '');

          state.isLoading = false;
          state.isLoggedIn = false;
          state.isRequireAuth = true;
          state.user = {};
        }
      })
      .addCase(fetchUser.rejected, state => {
        localStorage.setItem('ACCESS_TOKEN', '');
        localStorage.setItem('REFRESH_TOKEN', '');
        localStorage.setItem('USER', '');

        state.isLoading = false;
        state.isLoggedIn = false;
        state.isRequireAuth = true;
        state.user = {};
      });
  },
});

export const { testlogin, login, logout } = userSlice.actions;
export default userSlice.reducer;
