import axios from 'axios';

const baseURL = 'http://localhost:7700/api/v1/';

function setHeaderTokenAuth(req) {
  const token = localStorage.getItem('ACCESS_TOKEN');
  if (token) {
    req.headers.Authorization = `Bearer ${token}`;
  }
  return req;
}

function resolveResponse(response) {
  const { data, status } = response;
  return { data, status };
}

async function errorHandler(error) {
  const { data } = error.response;
  // if (status == 401) {
  //   try {
  //     const token = storage.getString(REFRESH_TOKEN);
  //     const res = await axios({
  //       url: `${baseURL}:8801/auth/token`,
  //       method: 'get',
  //       headers: {
  //         Authorization: `Bearer ${token}`,
  //       },
  //     });
  //     if (res.data?.access_token) {
  //       storage.setString(ACCESS_TOKEN, res.data.access_token);
  //     }
  //     if (res.data?.refresh_token) {
  //       storage.setString(REFRESH_TOKEN, res.data.refresh_token);
  //     }
  //   } catch (error) {
  //     console.error(error);
  //   }
  // }
  return Promise.reject(data);
}

function createAxios({ dev }) {
  const ret = axios.create({
    baseURL: dev,
  });
  ret.interceptors.request.use(setHeaderTokenAuth, errorHandler);
  ret.interceptors.response.use(resolveResponse, errorHandler);
  return ret;
}

const axiosApi = createAxios({ dev: baseURL });

export default axiosApi;
