import { createTheme } from '@mui/material/styles';
import { listItemClasses } from '@mui/material';

export const inputtheme = createTheme({
  palette: {
    primary: {
      main: '#1F2026',
      // contrastText: '#fff',
    },
  },
});

export const basiclogintheme = createTheme({
  palette: {
    primary: {
      main: '#78FF31',
      contrastText: '#000',
    },
  },
});

export const kakaotheme = createTheme({
  palette: {
    primary: {
      main: '#F7E600',
      contrastText: '#000',
    },
  },
});

export const googletheme = createTheme({
  palette: {
    primary: {
      main: '#ffffff',
      contrastText: '#000',
    },
  },
});

export const sx = {
  leftpaper: {
    width: 240,
    backgroundColor: '#101012',
  },
  rightpaper: {
    width: 'calc(100% - 240px)',
    backgroundColor: '#E5E5E5',
  },
  box: {
    marginTop: 4,
    marginBotton: 4,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    width: 100,
    height: 100,
  },
  list: {
    backgroundColor: '#101012',
    width: 240,
    color: '#717171',
    '& svg': {
      fill: '#717171',
    },
    [`& .Mui-selected, & .${listItemClasses.root}:hover`]: {
      color: '#fff',
      fontWeight: 'bold',
      backgroundColor: '#1F2026',
      '& svg': {
        fill: '#fff',
      },
    },
  },
  listtext: {
    color: '#717171',
    [`& .active, & .${listItemClasses.root}:hover`]: {
      color: '#fff',
    },
  },
  listbottom: {
    width: 240,
    marginTop: 'calc(10% + 60px)',
    position: 'fixed',
    bottom: 0,

    backgroundColor: '#101012',
    color: '#717171',
    '& svg': {
      fill: '#717171',
    },
    [`& .active, & .${listItemClasses.root}:hover`]: {
      color: '#fff',
      fontWeight: 'bold',
      backgroundColor: '#1F2026',
      '& svg': {
        fill: '#fff',
      },
    },
  },
  typo: {
    textAlign: 'center',
    fontFamily: 'Noto Sans KR',
    fontStyle: 'normal',
    fontWeight: 500,
    fontSize: 16,
    lineHeight: 4,
    color: '#FFFFFF',
  },
};
