import React, { useCallback, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { useMutation } from 'react-query';
import GoogleLogin from 'react-google-login';

import { Button, Box, Container, TextField } from '@mui/material';
import { ThemeProvider } from '@mui/material/styles';

import { Letdy } from '../../assets/images';

import axiosApi from '../../utils/api';
import { useAlert } from '../../hooks';
import { testlogin, login, fetchUser } from '../../store';

import {
  basiclogintheme,
  kakaotheme,
  googletheme,
  sx,
  inputtheme,
} from './styles';
import './styles.css';

const LoginScreen = () => {
  const dispatch = useDispatch();
  const { isLoggedIn } = useSelector(state => state.user);
  const navigate = useNavigate();

  const KAKAO_AUTH_URL =
    'https://kauth.kakao.com/oauth/authorize?client_id=8fdbb64df8e7837478b2ee99e9e66216&redirect_uri=http://localhost:3000/kakaologin&response_type=code';
  const GOOGLE_CLIENT_ID =
    '666196508024-c0544817fescc0b1bep1iap5ns1mf1gk.apps.googleusercontent.com';

  const { show, hide } = useAlert();

  const { mutateAsync } = useMutation(payload =>
    axiosApi.post('/users', payload),
  );

  const onConfirmAlert = useCallback(() => {
    hide();
  }, [hide]);

  const showErrorMessage = useCallback(
    ({ message = '' }) => {
      show({
        content: message,
        label: '확인',
        onConfirm: () => onConfirmAlert(),
      });
    },
    [show, onConfirmAlert],
  );

  useEffect(() => {
    if (isLoggedIn) {
      navigate('/');
    }
  }, [isLoggedIn]);

  const handleLogin = useCallback(
    result => {
      dispatch(login(result));
      dispatch(fetchUser());
    },
    [dispatch],
  );

  const handleBasicLogin = () => {
    dispatch(testlogin(true));
  };

  const handleKakaoLogin = () => {
    window.location.href = KAKAO_AUTH_URL;
  };

  const handleGoogleLoginSuccess = async res => {
    const { accessToken } = res;
    console.log(accessToken);

    try {
      if (accessToken) {
        const { data, status } = await mutateAsync({
          data: accessToken,
          identity_provider: 'kakao',
        });
        handleLogin({ data, status });
        navigate('/');
      }
    } catch (error) {
      console.error(error);
      showErrorMessage({ message: '구글 로그인에 실패했습니다.' });
    }
  };

  const handleGoogleLoginFailure = error => {
    console.log(error);
    showErrorMessage({ message: '구글 로그인에 실패했습니다.' });
  };

  return (
    <div>
      <Container component="main" maxWidth={false}>
        <Box sx={sx.box}>
          <img className="logo" alt="iPhone_01" src={Letdy} />
          <p className="logo_text">ADMIN</p>
          <div className="input">
            <p className="input_text">아이디</p>
            <ThemeProvider theme={inputtheme}>
              <TextField sx={sx.input} id="outlined-basic" variant="outlined" />
            </ThemeProvider>
            <p className="input_text">비밀번호</p>
            <ThemeProvider theme={inputtheme}>
              <TextField
                sx={sx.input}
                id="outlined-basic"
                variant="outlined"
                type="password"
              />
            </ThemeProvider>
          </div>
          <div className="button">
            <ThemeProvider theme={basiclogintheme}>
              <Button
                sx={sx.button}
                variant="contained"
                disabled={false}
                onClick={handleBasicLogin}
              >
                <p className="login_text">로그인</p>
              </Button>
            </ThemeProvider>
          </div>
          <div className="button">
            <ThemeProvider theme={kakaotheme}>
              <Button
                sx={sx.button}
                variant="contained"
                disabled={false}
                onClick={handleKakaoLogin}
              >
                <p className="login_text">카카오 로그인</p>
              </Button>
            </ThemeProvider>
          </div>
          <div>
            <GoogleLogin
              clientId={GOOGLE_CLIENT_ID}
              render={renderProps => (
                <ThemeProvider theme={googletheme}>
                  <Button
                    sx={sx.button}
                    variant="contained"
                    disabled={false}
                    onClick={renderProps.onClick}
                  >
                    <p className="login_text">구글 로그인</p>
                  </Button>
                </ThemeProvider>
              )}
              buttonText="구글 로그인"
              onSuccess={handleGoogleLoginSuccess}
              onFailure={handleGoogleLoginFailure}
            />
          </div>
        </Box>
      </Container>
    </div>
  );
};

export default LoginScreen;
