import React, { useMemo, createContext, useCallback, useState } from 'react';
import { Alert } from '../components';

const defaultValue = {
  visible: false,
  canClose: true,
  autoClose: false,
  content: '',
  description: '',
  label: '',
  onConfirm: () => {},
};

export const AlertContext = createContext(defaultValue);

export function AlertProvider({ children }) {
  const [alert, setAlert] = useState(defaultValue);

  const show = useCallback(options => {
    setAlert({ ...options, visible: true });
  }, []);

  const hide = useCallback(() => {
    setAlert(previous => ({ ...previous, visible: false }));
  }, []);

  const alterProviderValue = useMemo(() => ({ show, hide }), [show, hide]);

  return (
    <AlertContext.Provider value={alterProviderValue}>
      {children}
      <Alert {...alert} />
    </AlertContext.Provider>
  );
}
