import React, { useCallback, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { useMutation } from 'react-query';

import axios from 'axios';
import axiosApi from '../../utils/api';

import { useAlert } from '../../hooks';
import { login, fetchUser } from '../../store';

const KaKaoLoginScreen = () => {
  console.log('KaKaoLoginScreen');

  const { show, hide } = useAlert();
  const dispatch = useDispatch();
  const location = useLocation();
  const navigate = useNavigate();
  const KAKAO_CODE = location.search.split('=')[1];

  const { mutateAsync } = useMutation(payload =>
    axiosApi.post('/users', payload),
  );

  const onConfirmAlert = useCallback(() => {
    hide();
  }, [hide]);

  const showErrorMessage = useCallback(
    ({ message = '' }) => {
      show({
        content: message,
        label: '확인',
        onConfirm: () => onConfirmAlert(),
      });
    },
    [show, onConfirmAlert],
  );

  const handleLogin = useCallback(
    result => {
      dispatch(login(result));
      dispatch(fetchUser());
    },
    [dispatch],
  );

  const getKakaoToken = useCallback(async () => {
    const makeFormData = params => {
      const searchParams = new URLSearchParams();
      Object.keys(params).forEach(key => {
        searchParams.append(key, params[key]);
      });

      return searchParams;
    };

    try {
      const { data: kakaoauth } = await axios({
        method: 'POST',
        headers: {
          'content-type': 'application/x-www-form-urlencoded;charset=utf-8',
        },
        url: 'https://kauth.kakao.com/oauth/token',
        data: makeFormData({
          grant_type: 'authorization_code',
          client_id: '8fdbb64df8e7837478b2ee99e9e66216',
          redirect_uri: 'http://localhost:3000/kakaologin',
          code: KAKAO_CODE,
        }),
      });
      if (kakaoauth.access_token) {
        const { data, status } = await mutateAsync({
          data: kakaoauth.access_token,
          identity_provider: 'kakao',
        });
        handleLogin({ data, status });
        navigate('/');
      } else {
        navigate('/login');
      }
    } catch (error) {
      console.error(error);
      showErrorMessage({ message: '카카오 로그인에 실패했습니다.' });
      navigate('/login');
    }
  }, [showErrorMessage, mutateAsync, handleLogin]);

  useEffect(() => {
    if (!location.search) return;
    getKakaoToken();
  }, []);

  return (
    <div>
      <p>KaKaoLoginScreen</p>
    </div>
  );
};

export default KaKaoLoginScreen;
