import React from 'react';

import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from '@mui/material';
import { ThemeProvider } from '@mui/material/styles';

import { dialogtheme, dialogbuttontheme, sx } from './styles';

import { useConfirm } from '../../hooks';

export default function Confirm({
  visible = false,
  content = '',
  description = '',
  cancelLabel = '아니오',
  confirmLabel = '네',
  onConfirm = () => {},
}) {
  const { hide } = useConfirm();

  return (
    <ThemeProvider theme={dialogtheme}>
      <Dialog
        open={visible}
        onClose={onConfirm}
        aria-describedby="alert-dialog-slide-description"
      >
        <Box sx={sx.box}>
          <DialogTitle>{content}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-slide-description">
              {description}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <ThemeProvider theme={dialogbuttontheme}>
              <Button onClick={onConfirm}>{confirmLabel}</Button>
              <Button onClick={hide}>{cancelLabel}</Button>
            </ThemeProvider>
          </DialogActions>
        </Box>
      </Dialog>
    </ThemeProvider>
  );
}
