import React, { useMemo, createContext, useCallback, useState } from 'react';
import { Confirm } from '../components';

const defaultValue = {
  visible: false,
  canClose: true,
  content: '',
  description: '',
  onConfirm: () => {},
};

export const ConfirmContext = createContext(defaultValue);

export function ConfirmProvider({ children }) {
  const [confirm, setConfirm] = useState(defaultValue);

  const show = useCallback(options => {
    setConfirm({ ...options, visible: true });
  }, []);

  const hide = useCallback(() => {
    setConfirm(previous => ({ ...previous, visible: false }));
  }, []);

  const confirmProviderValue = useMemo(() => ({ show, hide }), [show, hide]);

  return (
    <ConfirmContext.Provider value={confirmProviderValue}>
      {children}
      <Confirm {...confirm} />
    </ConfirmContext.Provider>
  );
}
