import React, { useState } from 'react';
import { useDispatch } from 'react-redux';

import {
  Avatar,
  Box,
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Typography,
} from '@mui/material';
import AutoAwesomeMosaicIcon from '@mui/icons-material/AutoAwesomeMosaic';
import PersonOutlineIcon from '@mui/icons-material/PersonOutline';
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';
import LogoutIcon from '@mui/icons-material/Logout';

import { logout } from '../../store';
import { Letdy192 } from '../../assets/images';

import { sx } from './styles';
import DashboardScreen from '../DashboardScreen';
import UserManageScreen from '../UserManageScreen';
import ScheduleManageScreen from '../ScheduleManageScreen';

const HomeScreen = () => {
  console.log('HomeScreen');

  const [tabIndex, setTabIndex] = useState(0);
  const dispatch = useDispatch();

  const sidemenu = [
    { text: '대시보드', link: '/dashboard' },
    { text: '사용자 관리', link: '/user' },
    { text: '일정 관리', link: '/schedule' },
  ];

  const handleChange = (e, newTabIndex) => {
    setTabIndex(newTabIndex);
  };

  const handleLogout = () => {
    dispatch(logout());
  };

  return (
    <div>
      <Drawer
        variant="permanent"
        anchor="left"
        open
        PaperProps={{
          sx: sx.leftpaper,
        }}
      >
        <Box sx={sx.box}>
          <Avatar variant="circular" src={Letdy192} sx={sx.avatar} />
          <Typography sx={sx.typo}>관리자</Typography>
        </Box>
        <Box sx={sx.box}>
          <List sx={sx.list}>
            {sidemenu.map((menu, index) => (
              <ListItem key={menu.text} disablePadding to={menu.link}>
                <ListItemButton
                  selected={index === tabIndex}
                  autoFocus
                  onClick={e => handleChange(e, index)}
                >
                  <ListItemIcon>
                    {index === 0 && <AutoAwesomeMosaicIcon />}
                    {index === 1 && <PersonOutlineIcon />}
                    {index === 2 && <CalendarMonthIcon />}
                  </ListItemIcon>
                  <ListItemText primary={menu.text} />
                </ListItemButton>
              </ListItem>
            ))}
          </List>
        </Box>
        <Box>
          <List sx={sx.listbottom}>
            <ListItem key="logout">
              <ListItemButton onClick={handleLogout}>
                <ListItemIcon>
                  <LogoutIcon />
                </ListItemIcon>
                <ListItemText primary="로그아웃" />
              </ListItemButton>
            </ListItem>
          </List>
        </Box>
      </Drawer>
      <Drawer
        variant="permanent"
        anchor="right"
        open
        PaperProps={{
          sx: sx.rightpaper,
        }}
      >
        {tabIndex === 0 && <DashboardScreen />}
        {tabIndex === 1 && <UserManageScreen />}
        {tabIndex === 2 && <ScheduleManageScreen />}
      </Drawer>
    </div>
  );
};

export default HomeScreen;
