import React, { useEffect } from 'react';

import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from '@mui/material';
import { ThemeProvider } from '@mui/material/styles';

import { dialogtheme, dialogbuttontheme, sx } from './styles';

export default function Alert({
  visible = false,
  autoClose = false,
  content = '',
  description = '',
  label = '확인',
  onConfirm = () => {},
}) {
  useEffect(() => {
    const unsubscribe = setTimeout(() => {
      if (autoClose) {
        onConfirm();
      }
    }, 2500);

    return () => unsubscribe;
  }, [onConfirm, autoClose]);

  return (
    <ThemeProvider theme={dialogtheme}>
      <Dialog
        open={visible}
        onClose={onConfirm}
        aria-describedby="alert-dialog-slide-description"
      >
        <Box sx={sx.box}>
          <DialogTitle>{content}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-slide-description">
              {description}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <ThemeProvider theme={dialogbuttontheme}>
              <Button variant="contained" disabled={false} onClick={onConfirm}>
                {label}
              </Button>
            </ThemeProvider>
          </DialogActions>
        </Box>
      </Dialog>
    </ThemeProvider>
  );
}
